a {
  text-decoration: none;
  color: black;
  @media screen and (hover: hover) {
    &:hover {
      text-decoration: underline;
    }
  }
}
.o-v{
  overflow: visible !important;
}
.d-none{
  display:none !important;
}
button {
  border: none;
  border-radius: 10px;
  padding: 5px 15px;
  &.primary{
    color:purple;
    background: #fff;
  }
  &.flat{
    background: none;
    &.light{
      color:#fff;
    }
    &.dark{
      color:#1d1d1d;
    }
  }
}